<template>
    <div id="search">
        <headerBox :headerData="headerData"></headerBox>
        <div class="titlebar">
            <div class="container clear">
                <h1 v-if="(searchCnt.type || routeQuery.type) == '_s'">"{{searchCnt.text || routeQuery.text}}"ÁöÑÊêúÁ¥¢ÁªìÊûú</h1>
                <h1 v-else>{{(searchCnt.type || routeQuery.type) == 'Category' ? 'ÂàÜÁ±ª' : (searchCnt.type || routeQuery.type)}}Ôºö{{searchCnt.text || routeQuery.text}}</h1>
                <p class="breadcrumbs">
                    <router-link class="u_transition_300 u_hover_active" :to="{path: '/blog'}">ÂçöÂÆ¢</router-link>
                    <span>&gt;</span>
                    <strong v-if="(searchCnt.type || routeQuery.type) == '_s'">"{{searchCnt.text || routeQuery.text}}"</strong>
                    <strong v-else>{{searchCnt.text || routeQuery.text}}</strong>
                </p>
            </div>
        </div>
        <articleList :searchCnt="searchCnt"></articleList>
        <footerBox :blogPage="true"></footerBox>
    </div>
</template>

<script>
"use strict";
import headerBox  from '../layout/header.vue'
import footerBox  from '../layout/footer.vue'
import articleList from './articleList.vue'

export default {
    mounted() {
        // Âà§Êñ≠ ÊêúÁ¥¢ËÆ∞ÂΩïÊòØÂê¶Â≠òÂú®
        if(!this.$store.state.searchCnt.text) {
            for (var i in this.$route.query) {
                this.routeQuery = {type: i,text: this.$route.query[i]};
            }

            if (!this.routeQuery.type)
                this.routeQuery = {type: "Category",text: "ÂÖ®ÈÉ®"};
        };
    },
    data() {
        return {
            headerData: {
                title: 'Search Result',
                searchStatus: true,
                isStatic: true,
                type: 'blog'
            },
            routeQuery: {}
        }
    },
    computed: {
        searchCnt() {
            return this.$store.state.searchCnt;
        }
    },
    components: {
        headerBox,
        footerBox,
        articleList
    }
}
</script>

<style lang="scss" scoped>
	#search {
		.titlebar {
            position: relative;
            top: -1px;
			background-color: #fff;
            .container {
                min-height: 60px;
                margin: 0 auto;
                line-height: 60px;
            }
			h1 {
				float: left;
				font-size: 24px;
	    		font-weight: normal;
			}
		}

		.breadcrumbs {
			float: right;
			a,strong {
				color: #8d8d8d;
			}
			span {
				padding: 0 10px;
			}
		}
	}

    #rightBox .box {
        background-color: null;
    }
</style>


